<template>
  <div
    v-show="$node.filtered"
    :class="className"
    class="gs-tree-node"
  >
    <div
      :class="{
        'is-active': $node.isActive,
        'is-disabled': node.data[disabled] && disableNode,
        'is-hover-highlight': hoverHighlight,
        ['pl-' + $node.level]: indent === 24
      }"
      :style="[indent !== 24 ? {'padding-left': (($node.level - 1) * indent || 0) + 'px'}: '']"
      :level="$node.level"
      class="gs-tree-node-content"
      @click="handleNodeClick"
    >
      <gs-icon
        :name="icon"
        :disabled="node.data[expandDisabled]"
        :class="{
          'is-expand': $node.isExpand,
          'is-leaf': $node.isLeaf && $node.loaded
        }"
        class="gs-tree-node-expand-icon"
        mode="button"
      />
      <gs-checkbox
        v-if="hasCheckbox"
        :value="$node.checked"
        :indeterminate="indeterminate"
        :disabled="node.data[disabled]"
        @change="handleCheckChange"
        @click.native.stop
      />
      <gs-icon
        v-if="$node.loading"
        name="loading"
        class="gs-tree-node-loading-icon"
      />
      <node-content :node="node" />
    </div>
  </div>
</template>

<script>
import {
  Checkbox,
  Icon
} from '@gs-ui/gs-ui';
export default {
  name: 'TreeNode',
  components: {
    [Checkbox.name]: Checkbox,
    [Icon.name]: Icon,
    nodeContent: {
      props: ['node'],
      methods: {
        handleContentClick(e) {
          this.$parent.root.$emit('content-click', this.node, e);
        }
      },
      render(h) {
        const [node, parent, label] = [this.node, this.$parent, this.$parent.props.label];
        return (
          parent.renderContent
            ? parent.renderContent.call(parent._renderProxy, h, {node, data: node.data})
            : (parent.showTips
              ? <span class="is-ellipsis" title={node.data[label]} on-click={this.handleContentClick}>{ node.data[label] }</span>
              : <span class="is-ellipsis" on-click={this.handleContentClick}>{ node.data[label] }</span>)
        );
      }
    }
  },
  props: {
    node: {
      default() {
        return {};
      }
    },
    props: Object,
    showCheckbox: {
      type: Boolean,
      default: false
    },
    leafCheckboxOnly: Boolean,
    renderContent: Function,
    treeStore: Object,
    root: Object,
    indent: Number,
    icon: String,
    expandOnClickNode: Boolean,
    disableNode: Boolean,
    hoverHighlight: Boolean,
    showTips: Boolean
  },
  computed: {
    hasCheckbox() {
      const {
        node,
        showCheckbox,
        leafCheckboxOnly
      } = this;
      let isShow = showCheckbox;

      if (leafCheckboxOnly && node.childNodes && node.childNodes.length > 0) {
        isShow = false;
      }

      return isShow;
    },
    className() {
      return this.node.data[this.props.className];
    },
    disabled() {
      return this.props.disabled;
    },
    expandDisabled() {
      return this.props.expandDisabled;
    },
    $node() {
      return this.node.$node;
    },
    indeterminate() {
      const { $node } = this;
      return !$node.isLeaf && $node.leafCount > $node.checkedCount && $node.checkedCount > 0;
    }
  },
  watch: {
    '$node.checkedCount'(nowCount, oldCount) {
      if (nowCount === 0 || nowCount === this.$node.leafCount) {
        this.root.$emit('check-change', this.node.data, false, false);
      } else if (oldCount === 0 || oldCount === this.$node.leafCount) {
        this.root.$emit('check-change', this.node.data, false, true);
      }
    },
    '$node.checked'(checked) {
      if (this.$node.isLeaf || this.treeStore.lazy) {
        this.root.$emit('check-change', this.node.data, checked, this.indeterminate);
        this.root.$emit('current-change', this.node, checked, this.indeterminate);
      }
    },
    '$node.isExpand'(isExpand) {
      this.$emit('node-toggle', this.node, isExpand);
      this.root.$emit('node-toggle', this.node, isExpand);
    },
    indeterminate(val) {
      if (!this.$node.isLeaf) {
        this.root.$emit('check-change', this.node.data, this.$node.checked, val);
        this.root.$emit('current-change', this.node, this.$node.checked, val);
      }
    }
  },
  methods: {
    handleNodeClick(ev) {
      let flag = 0;
      const {
        node,
        $node,
        expandDisabled,
        disabled,
        disableNode,
        expandOnClickNode,
        treeStore,
        root
      } = this;

      if (!node.data[expandDisabled]) {
        if (expandOnClickNode || ev.target.classList.contains('gs-tree-node-expand-icon')) {
          // 展开节点
          this.toggleNodeExpander(!$node.isExpand);
          flag++;
        }
      }
      if (!node.data[disabled] || !disableNode) {
        if (!ev.target.classList.contains('gs-tree-node-expand-icon') || $node.isLeaf) {
          // 选中节点
          treeStore.toggleCurrentActive($node.key);
        }
        flag++;
      }
      if (flag) {
        root.$emit('node-click', node, ev);
      }
    },
    handleCheckChange(e) {
      let checked = e.target.checked;
      const { node, treeStore, indeterminate, $node } = this;
      if (!checked && indeterminate) {
        checked = true;
      }
      treeStore.setChecked(node, checked);
    },
    toggleNodeExpander(isExpand) {
      const {
        node,
        $node,
        expandDisabled,
        treeStore
      } = this;

      if (!node.data[expandDisabled]) {
        // 展开节点
        treeStore.setExpanded(node, isExpand);
        if ($node.isExpand && treeStore.lazy) {
          treeStore.handleLazyLoad(node);
        }
        if (treeStore.accordion) {
          treeStore.makeAccordion(node);
        }
      }
    }
  }
};
</script>
