import { findChild, findParent, error, shouldShowNodeByExpanded } from './index';

export default class Tree {
  constructor(options) {
    this.data = options.data;
    this.checkedKeys = options.defaultCheckedKeys.slice();
    this.expandedKeys = options.defaultExpandedKeys.slice();
    this.vm = options.vm;
    this.nodeKey = options.nodeKey;
    this.props = options.props;
    this.tree = [];
    this.lazy = options.lazy;
    this.accordion = options.accordion;
    this.allExpand = options.allExpand;
    this.delayExpandKey = options.delayExpandKey;


    this.nodesMap = {};

    this.lastNodeKeys = [];
    this.createTree(this.data, null, this.tree);
    this.setRoot();
    if (this.allExpand) {
      this.tree.forEach(node => {
        this.expandedKeys.push(node.$node.key);
        findChild(node, child => {
          this.expandedKeys.push(child.$node.key);
        });
      });
      this.expandedKeys = Array.from(new Set([...this.expandedKeys]));
    }
    this.initCheckedKeys(this.checkedKeys);
    this.initExpandedKeys(this.expandedKeys);
    this.deleteUnuseKeys();
    this.findLastNodeKey();
  }
  createTree(data) {
    const genTree = (data, parent, target, level) => {
      data.forEach(item => {
        const $node = {
          parent,
          level,
          checked: false,
          isExpand: false,
          isLeaf: !(item[children] && item[children].length),
          isActive: false,
          filtered: true,
          visible: !parent ? true : this.allExpand,
          indeterminate: false
        };
        item[children] && item[children].length && ($node.checkedCount = 0);

        if (this.nodeKey) {
          $node.key = item[this.nodeKey];
        } else if (item.__key__) {
          $node.key = item.__key__;
          delete item.__key__;
        } else {
          $node.key = nodeId++;
        }

        if (this.lazy) {
          item.loaded = $node.loaded = item.loaded || item.leaf || false;
          if (item[children] && item[children].length) {
            $node.loaded = item.loaded = true;
          }
          $node.loading = false;
        } else {
          $node.loaded = true;
        }
        const index = target.push({
          data: item,
          $node,
          $key: $node.key
        });
        this.nodesMap[$node.key] = target[index - 1];
        if (item[children] && item[children].length) {
          // debugger
          let _target = target[index - 1];
          if (_target.childNodes) {
            _target = this.tree[this.tree.length - 1];
          }
          _target.childNodes = [];
          genTree(item[children], _target, _target.childNodes, level + 1);
        } else {
          // leaf
          findParent(
            target[index - 1],
            parent => {
              parent.$node.leafCount = parent.$node.leafCount || 0;
              const count = parent.$node.leafCount + 1;
              parent.$node.leafCount = count;
            },
            true
          );
        }
      });
    };
    let [level, nodeId] = [1, 1];
    this.data.forEach(node => {
      findChild(
        node,
        child => {
          if (child.__key__ >= nodeId) {
            nodeId = child.__key__ + 1;
          }
        },
        true
      );
      if (node.__key__ >= nodeId) {
        nodeId = node.__key__ + 1;
      }
    });
    const children = this.props.children;
    genTree(data, null, this.tree, level);
  }
  setRoot() {
    this.tree.forEach(root => {
      findChild(root, child => {
        child.$node.root = root;
      });
      root.$node.root = root;
    });
  }
  initChecked(val) {
    this.tree.forEach(node => {
      findChild(
        node,
        child => {
          if (child.$node.key === val) {
            this.setChecked(child, true, true);
          }
        },
        true
      );
      if (node.$node.key === val) {
        this.setChecked(node, true, true);
      }
    });
  }
  initExpand(val) {
    this.tree.forEach(node => {
      findChild(
        node,
        child => {
          if (child.$node.key === val) {
            this.setExpanded(child, true);
          }
        },
        true
      );
      if (node.$node.key === val) {
        this.setExpanded(node, true);
      }
    });
  }
  setChecked(node, checked, init) {
    if (!node) {
      return;
    }
    if (node.$node.isLeaf) {
      let isChanged = false;
      if (!node.data[this.props.disabled] || init) {
        if (node.$node.checked !== checked) {
          isChanged = true;
          node.$node.checked = checked;
          const key = node.$node.key;
          const index = this.checkedKeys.indexOf(key);
          if (checked && index === -1) {
            this.checkedKeys.push(key);
          } else if (!checked && index !== -1) {
            this.checkedKeys.splice(index, 1);
          }
        }
      }
      // notify all parents
      if (!this.vm.leafCheckboxOnly) {
        findParent(
          node,
          parent => {
            const count = checked
              ? Math.min((parent.$node.checkedCount || 0) + (isChanged ? 1 : 0), parent.$node.leafCount)
              : Math.max((parent.$node.checkedCount || 0) - (isChanged ? 1 : 0), 0);
            parent.$node.checkedCount = count;
            parent.$node.checked = checked
              ? parent.$node.checkedCount > 0
              : parent.$node.checkedCount !== 0;
          },
          true
        );
      }
    } else {
      findChild(node, child => this.setChecked(child, checked, init), false);
    }
  }
  setExpanded(node, expanded) {
    const key = node.$node.key;
    const index = this.expandedKeys.indexOf(key);
    if (key === this.delayExpandKey) {
      this.vm.$nextTick(() => {
        node.$node.isExpand = expanded;
        this.delayExpandKey = null;
        this.updateExpandedKeys(node, expanded, index, key);
      });
    } else {
      node.$node.isExpand = expanded;
      this.updateExpandedKeys(node, expanded, index, key);
    }
  }
  updateExpandedKeys(node, expanded, index, key) {
    if (expanded && index === -1) {
      this.expandedKeys.push(key);
    } else if (!expanded && index !== -1) {
      this.expandedKeys.splice(index, 1);
    }
    findChild(node, child => {
      const parentsKey = [];
      findParent(
        child,
        parent => {
          parentsKey.push(parent.$key);
        },
        true
      );
      child.$node.visible = shouldShowNodeByExpanded(this.expandedKeys, parentsKey);
    }, true);
  }
  initCheckedKeys(keys) {
    keys.forEach(val => this.initChecked(val, true));
  }
  initExpandedKeys(keys) {
    keys.forEach(val => this.initExpand(val, true));
  }
  deleteUnuseKeys() {
    const { checkedKeys, expandedKeys } = this;
    const [newCheckedKeys, newExpandedKeys] = [
      checkedKeys.slice(),
      expandedKeys.slice()
    ];
    checkedKeys.forEach((key, index) => {
      let flag = false;
      this.tree.forEach(node => {
        findChild(
          node,
          child => {
            if (child.$node.isLeaf && child.$node.key === key) {
              flag = true;
            }
          },
          true
        );
        if (node.$node.isLeaf && node.$node.key === key) {
          flag = true;
        }
        if (this.lazy && !node.$node.isLeaf && node.$node.key === key) {
          newCheckedKeys.splice(index, 1);
        }
      });
      // if (!flag) {
      //   newCheckedKeys.shift();
      // }
    });
    expandedKeys.forEach((key, index) => {
      let flag = false;
      this.tree.forEach(node => {
        findChild(
          node,
          child => {
            if (child.$node.key === key) {
              flag = true;
            }
          },
          true
        );
        if (node.$node.key === key) {
          flag = true;
        }
      });
      if (!flag) {
        newExpandedKeys.shift();
      }
    });
    this.checkedKeys = newCheckedKeys;
    this.expandedKeys = newExpandedKeys;
  }
  toggleCurrentActive(key) {
    this.tree.forEach(node => {
      findChild(node, child => {
        child.$node.isActive = child.$node.key === key;
      });
      node.$node.isActive = node.$node.key === key;
    });
  }
  handleLazyLoad(node) {
    if (!node.$node.loaded && this.lazy) {
      if (!this.vm.load) {
        process.env.NODE_ENV !== 'production' && error('Tree', 'load function is required for lazy load');
        return;
      }
      node.$node.loading = true;
      this.vm.load(node, data => {
        // for expand delay
        this.delayExpandKey = node.$node.key;
        if (!this.nodeKey) {
          // save keys
          [this.expandedKeys, this.checkedKeys].forEach(keys => {
            keys.forEach((key, index, arr) => {
              if (typeof key === 'number') {
                this.tree.forEach(node => {
                  findChild(
                    node,
                    child => {
                      if (child.$node.key === key) {
                        child.data.__key__ = child.$node.key = key;
                      }
                    },
                    true
                  );
                  if (node.$node.key === key) {
                    node.data.__key__ = node.$node.key = key;
                  }
                });
              }
            });
          });
        }
        this.vm.$set(node.data, 'children', []);
        node.$node.loaded = node.data.loaded = true;
        node.$node.loading = false;
        if (!Array.isArray(data) || !data.length) {
          node.data.leaf = true;
        }
        node.data.children = data;
        this.setExpanded(node, true);
      });
    }
  }
  getCheckedLeafNodes(notConsidedFilter) {
    const arr = [];
    this.checkedKeys.forEach(key => {
      // 节点的filtered属性，当赋值为true时，代表被过滤后保留的节点
      if (this.nodesMap[key].$node.filtered || notConsidedFilter) {
        arr.push(this.nodesMap[key]);
      }
    });
    return arr;
  }
  getCheckedLeafKeys(notConsidedFilter) {
    return this.checkedKeys.filter(key => this.nodesMap[key].$node.filtered || notConsidedFilter);
  }
  getCheckedNodes(notConsidedFilter) {
    const arr = [];
    Object.values(this.nodesMap).forEach(node => {
      if (
        (node.$node.isLeaf && node.$node.checked && (node.$node.filtered || notConsidedFilter)) ||
        (!node.$node.isLeaf &&
          node.$node.checkedCount === node.$node.leafCount &&
          (node.$node.filtered || notConsidedFilter))
      ) {
        arr.push(node);
      }
    });
    return arr;
  }
  getCheckedNodeKeys(notConsidedFilter) {
    const keys = [];
    Object.keys(this.nodesMap).forEach(key => {
      const node = this.nodesMap[key];
      if ((node.$node.isLeaf && node.$node.checked && (node.$node.filtered || notConsidedFilter)) || (!node.$node.isLeaf && node.$node.checkedCount === node.$node.leafCount && (node.$node.filtered || notConsidedFilter))) {
        keys.push(node.$node.key);
      }
    });
    return keys;
  }
  filterNode(val) {
    this.tree.forEach(node => {
      node.$node.filtered = this.vm.filterNodeMethod(val, node.data);
      findChild(node, child => {
        const result = this.vm.filterNodeMethod(val, child.data);
        child.$node.filtered = result;
        let flag = true;
        if (child.$node.loaded === false) {
          flag = false;
        }
        if (this.vm.expandOnFilterMatch && flag) {
          this.setExpanded(child, result);
        }
        if (result) {
          findParent(child, parent => {
            parent.$node.filtered = true;
            let flag = true;
            if (parent.$node.loaded === false) {
              flag = false;
            }
            if (this.vm.expandOnFilterMatch && flag) {
              this.setExpanded(parent, true);
            }
          });
        }
      });
    });
    if (this.vm.expandOnFilterMatch && !val && !this.allExpand) {
      this.setAllExpandFalse();
    }
  }
  makeAccordion(_node) {
    const { level, key } = _node.$node;
    Object.values(this.nodesMap).forEach(node => {
      if (node.$node.level === level && node.$node.key !== key) {
        this.setExpanded(node, false);
      }
    });
  }
  setCheckedKeys(keys, leafOnly) {
    // this.expandedKeys = [];
    Object.values(this.nodesMap).forEach(node => {
      if (node.$node.isLeaf) {
        this.setChecked(node, false);
      }
    });
    keys.forEach(key => {
      const node = this.nodesMap[key];

      if (leafOnly) {
        if (node && node.$node.isLeaf) {
          this.setChecked(node, true);
        }
      } else {
        this.setChecked(node, true);
      }
    });
  }
  setExpandedKeys(keys) {
    if (this.allExpand) {
      return;
    }
    this.expandedKeys = keys;
    this.setAllExpandFalse();
    this.initExpandedKeys(keys);
  }
  setAllExpandFalse() {
    this.tree.forEach(node => {
      node.$node.isExpand = false;
      findChild(node, child => {
        child.$node.isExpand = false;
      });
    });
    this.expandedKeys = [];
  }
  findLastNodeKey() {
    const fn = (tree) => {
      const popNode = tree[tree.length - 1];
      if (popNode) this.lastNodeKeys.push(popNode.$node.key);
      tree.forEach(node => {
        if (node.childNodes) fn(node.childNodes);
      });
    };
    fn(this.tree);
  }
  setIndeterminateKeys(keys) {
    keys.forEach(val => {
      this.tree.forEach(node => {
        findChild(
          node,
          child => {
            if (child.$node.key === val) {
              child.$node.indeterminate = true;
            }
          },
          true
        );
        if (node.$node.key === val) {
          node.$node.indeterminate = true;
        }
      });
    });
  }
}
