import intersection from 'lodash.intersection';
export function findChild(node, fn, deep = true) {
  (node.childNodes || node.children || []).forEach(child => {
    typeof fn === 'function' && fn(child);
    deep && findChild(child, fn, deep);
  });
}

export function findParent(node, fn, deep = true) {
  const {
    parent
  } = node.$node;
  if (parent) {
    typeof fn === 'function' && fn(parent);
    deep && findParent(parent, fn, deep);
  }
}
export function error(component, str) {
  console.error(`[Component Error][${component}]: ${str}`);
}

export function eqArray(arr, other) {
  if (!Array.isArray(arr) || !Array.isArray(other)) {
    return false;
  }
  if (arr.length !== other.length) {
    return false;
  }
  for (let i = 0; i < arr.length; i++) {
    if (arr[i] !== other[i]) {
      return false;
    }
  }
  return true;
}
export function flatTree(data = [], dep = Infinity, children = 'childNodes') {
  const result = [];
  (function flat(nodes) {
    if (nodes.length === 0) {
      return;
    }
    if (dep >= 0) {
      for (let i = 0; i < nodes.length; i++) {
        result.push(nodes[i]);
        if (nodes[i][children]) {
          dep--;
          flat(nodes[i][children], dep);
          dep++;
        };
      }
    }
  })(data);
  return result;
}
export function getExpandedChidlLength(node) {
  let count = 0;
  (function getExpandedLen(data) {
    const len = data.childNodes.length;
    for (let i = 0; i < len; i++) {
      if (data.childNodes[i].childNodes && data.childNodes[i].$node.isExpand) {
        count++;
        count = count + data.childNodes[i].childNodes.length;
        getExpandedLen(data.childNodes[i].childNodes);
      }
    }
  })(node);
  return count;
}
export function shouldShowNodeByExpanded(expandItemValues, parent) {
  const intersectionKeys = intersection(expandItemValues, parent);
  if (intersectionKeys.length === parent.length) {
    return true;
  }
  return false;
}
