
<template>
  <div
    :style="{'height': calcHeight}"
    class="gs-infinite-tree"
  >
    <RecycleScroller
      :items="treeList"
      :item-size="32"
      class="gs-infinite-tree-scroller"
      key-field="$key"
      @resize="resize"
      @visible="visible"
      @hidden="hidden"
      @update="update"
      @scroll.native="onScroll"
    >
      <template v-slot="{item, index}">
        <tree-node
          :key="index"
          :render-content="renderContent"
          :node="item"
          :show-checkbox="showCheckbox"
          :leaf-checkbox-only="leafCheckboxOnly"
          :indent="indent"
          :props="curProps"
          :tree-store="treeStore"
          :root="root"
          :icon="icon"
          :expand-on-click-node="expandOnClickNode"
          :disable-node="disableNode"
          :hover-highlight="hoverHighlight"
          :show-tips="showTips"
          @node-toggle="nodeToggle(arguments, index)"
        />
        <div
          v-if="showLoadMoreBtn && showLoadMore(item)"
          :style="{'padding-left': ((item.$node.level + 2) * indent || 0) + 'px'}"
          class="load-more"
          @click="clickLoadMore(item)"
        >
          加载更多
        </div>
      </template>
    </RecycleScroller>
    <div
      v-if="!data.length"
      class="gs-tree-empty"
    >
      {{ emptyText }}
    </div>
  </div>
</template>

<script>
import TreeNode from './tree-node.vue';
import TreeStore from './util/store.js';
import { error, eqArray, flatTree } from './util/index';
import './style.scss';
import { RecycleScroller } from 'vue-virtual-scroller';
import 'vue-virtual-scroller/dist/vue-virtual-scroller.css';
export default {
  name: 'GsInfiniteTree',
  components: {
    TreeNode,
    'RecycleScroller': RecycleScroller
  },
  props: {
    data: {
      type: Array,
      required: true
    },
    props: {
      type: Array,
      default: () => {}
    },
    height: {
      type: [Number, String],
      default: 300
    },
    // eslint-disable-next-line vue/require-default-prop
    renderContent: Function,
    emptyText: {
      type: String,
      default: '暂无数据'
    },
    defaultCheckedKeys: {
      type: Array,
      default: () => []
    },
    defaultExpandedKeys: {
      type: Array,
      default: () => []
    },
    nodeKey: {
      type: String,
      default: ''
    },
    showCheckbox: {
      type: Boolean,
      default: false
    },
    leafCheckboxOnly: {
      type: Boolean,
      default: false
    },
    lazy: {
      type: Boolean,
      default: false
    },
    // eslint-disable-next-line vue/require-default-prop
    load: Function,
    // eslint-disable-next-line vue/require-default-prop
    filterNodeMethod: Function,
    accordion: {
      type: Boolean,
      default: false
    },
    defaultAllExpand: {
      type: Boolean,
      default: false
    },
    indent: {
      type: Number,
      default: 24
    },
    icon: {
      type: String,
      default: 'caret-right-sm'
    },
    expandOnFilterMatch: {
      type: Boolean,
      default: false
    },
    expandOnClickNode: {
      type: Boolean,
      default: true
    },
    disableNode: {
      type: Boolean,
      default: false
    },
    hoverHighlight: {
      type: Boolean,
      default: true
    },
    showTips: {
      type: Boolean,
      default: false
    },
    showLoadMoreBtn: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      treeStore: {},
      root: {},
      filterKey: '',
      treeList: [],
      treeListStore: []
    };
  },
  computed: {
    curProps() {
      return Object.assign({
        label: 'label',
        children: 'children',
        disabled: 'disabled',
        className: 'className',
        expandDisabled: 'expandDisabled'
      }, this.props || {});
    },
    treeMap() {
      const result = {};
      function flat(nodes) {
        if (!nodes || nodes.length === 0) return [];
        nodes.forEach(node => {
          result[node.$node.key] = node;
          return flat(node.childNodes);
        });
      }
      flat(this.treeStore.tree);
      return result;
    },
    calcHeight() {
      return typeof this.height === 'number' ? this.height + 'px' : this.height;
    }
  },
  watch: {
    data: {
      deep: true,
      handler() {
        this.initTree();
        this.root = this;
        this.$nextTick(() => {
          if (this.filterKey) {
            this.filter(this.filterKey);
          }
        });
      }
    },
    defaultExpandedKeys(val, old) {
      if (Array.isArray(val)) {
        if (process.env.NODE_ENV !== 'production' && !this.nodeKey) {
          error('Tree', 'nodeKey is required for defaultExpandedKeys');
        }
        if (!eqArray(val, old)) {
          this.treeStore.setExpandedKeys(val);
        }
      }
    },
    defaultCheckedKeys(val, old) {
      if (Array.isArray(val)) {
        if (process.env.NODE_ENV !== 'production' && !this.nodeKey) {
          error('Tree', 'nodeKey is required for defaultCheckedKeys');
        }
        if (!eqArray(val, old)) {
          this.treeStore.setCheckedKeys(val);
        }
      }
    }
  },
  created() {
    this.initTree();
    this.root = this;
  },
  methods: {
    initTree() {
      this.treeStore = new TreeStore({
        data: this.data,
        defaultCheckedKeys: this.treeStore.checkedKeys || this.defaultCheckedKeys,
        defaultExpandedKeys: this.treeStore.expandedKeys || this.defaultExpandedKeys,
        vm: this,
        nodeKey: this.nodeKey,
        props: this.curProps,
        lazy: this.lazy,
        accordion: this.accordion,
        allExpand: this.defaultAllExpand,
        delayExpandKey: this.treeStore.delayExpandKey
      });
      this.treeListStore = Object.freeze(flatTree(this.treeStore.tree, Infinity));
      this.treeList = this.treeListStore.filter(i => i.$node.visible);
    },
    getCheckedNodes(leafOnly, notConsidedFilter) {
      return leafOnly ? this.treeStore.getCheckedLeafNodes(notConsidedFilter) : this.treeStore.getCheckedNodes(notConsidedFilter);
    },
    getCheckedKeys(leafOnly, notConsidedFilter) {
      if (process.env.NODE_ENV !== 'production' && !this.nodeKey) {
        error('Tree', 'nodeKey is required for getCheckedKeys');
      }
      return leafOnly ? this.treeStore.getCheckedLeafKeys(notConsidedFilter) : this.treeStore.getCheckedNodeKeys(notConsidedFilter);
    },
    filter(val) {
      this.filterKey = val;
      this.treeStore.filterNode(val);
    },
    setCheckedKeys(keys, leafOnly) {
      this.treeStore.setCheckedKeys(keys, leafOnly);
    },
    setIndeterminateKeys(keys) {
      this.treeStore.setIndeterminateKeys(keys);
    },
    setChecked(key, checked) {
      if (this.treeStore.nodesMap[key].$node.checked === !!checked) {
        return;
      }
      this.treeStore.setChecked(this.treeStore.nodesMap[key], !!checked);
    },
    nodeToggle(args, index) {
      this.treeList = this.treeListStore.filter(i => i.$node.visible);
    },
    showLoadMore(item) {
      return this.treeStore.lastNodeKeys.includes(item.$node.key);
    },
    clickLoadMore(item) {
      this.$emit('load-more', item);
    },
    resize(args) {
      this.$emit('resize', args);
    },
    visible(args) {
      this.$emit('visible', args);
    },
    hidden(args) {
      this.$emit('hidden', args);
    },
    update(args) {
      this.$emit('update', args);
    },
    onScroll(args) {
      this.$emit('scroll', args);
    }
  }
};
</script>
